import React, { Component } from 'react';
import { Card, CardImg, CardText, CardBody, CardTitle, Col, Row, Breadcrumb, BreadcrumbItem, Modal, ModalBody, ModalHeader, Button, Label } from 'reactstrap';
import {Link} from 'react-router-dom';
import { Control, LocalForm, Errors } from 'react-redux-form';
import { Loading } from './LoadingComponent';
import { baseUrl } from '../shared/baseUrl';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';

const required = (val) => val && val.length;
const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => (val) && (val.length >= len);

function RenderDish({dish}) {
    return(
        <FadeTransform in 
            transformProps={{
                exitTransform: 'scale(0.5) translateY(-50)'
            }}>
        <Card>
            <CardImg src={baseUrl+dish.image} />
            <CardBody>
                <CardTitle>{dish.name}</CardTitle>
                <CardText>{dish.description}</CardText>
            </CardBody>
        </Card>
        </FadeTransform>
    )
}

function RenderComment({comments, postComment, dishId}) {
    if (comments != null)
        return(
            <div className="container">
                <h4 className="row m-2">Comments</h4>
                <div className="row">
                    <ul className="list-unstyled">
                    <Stagger in>
                    {comments.map((comm)=>{
                        var date = new Date(comm.date);
                        const months = ["Jan","Feb","March","April","June","July","Aug","Sep","Oct","Nov","Dec"]
                        return(
                            <Fade in>
                            <li className="container" key={comm.id}>
                                <div className="row m-2">
                                    {comm.comment}</div>
                                <div className="row m-2">-- {comm.author}, {months[date.getMonth()-1]} {date.getDate()}, {date.getFullYear()}</div>
                            </li>
                            </Fade>
                            
                        )
                    })}
                    </Stagger>
                    </ul>
                    <CommentForm dishId={dishId} postComment={postComment}/>
                </div>
            </div>
        );
    else
        return(
            <div></div>
        );
}

const DishDetail = (props)=>{
    if(props.isLoading){
        return(
            <div className="container">
            <div className="row">
            <Loading /></div>
            </div>
        );
    }
    else if(props.errMess){
        return(
            <div className="container">
            <div className="row">
            <h4>{props.errMess}</h4>
            </div>
            </div>
        );
    }
    if (props.dish != null)
        return(
            <div className="container">
                <div className="row">
                    <Breadcrumb>
                        <BreadcrumbItem><Link to="/menu">Menu</Link></BreadcrumbItem>
                        <BreadcrumbItem active>{props.dish.name}</BreadcrumbItem>
                    </Breadcrumb>
                    <div className="col-12">
                        <h3>{props.dish.name}</h3>
                        <hr/>
                    </div>
                    <div className="col-12 col-md-5 m-1">
                        <RenderDish dish = {props.dish} />
                    </div>
                    <div className="col-12 col-md-5 m1">
                        <RenderComment comments = {props.comments}
                        postComment={props.postComment}
                        dishId={props.dish.id}/>
                        <div className="col-12 col-md-5 m1">                        
                        </div>
                    </div>                    
                </div>                
            </div>
        );
    else
        return(
            <div></div>
        );
    
}

class CommentForm extends Component{
    constructor(props){
        super(props);
        this.state = {
            isOpen:false
        };
        this.toggleModal = this.toggleModal.bind(this);
    }

    toggleModal(){
        this.setState({
            isOpen:!this.state.isOpen
        });
    }

    handleSubmit(values){
        this.toggleModal();
        this.props.postComment(this.props.dishId, values.rating, values.author, values.comment)
    }

    render(){
        return(
            <React.Fragment>
            <Button outline onClick={this.toggleModal}><span className="fa fa-pencil"> Submit Comment</span></Button>
            <Modal isOpen={this.state.isOpen} toggle={this.toggleModal}>
                <ModalHeader toggle={this.toggleModal}>Submit Comment</ModalHeader>
                <ModalBody>
                    <LocalForm onSubmit={(values)=>this.handleSubmit(values)}>
                        <Row className="form-group">
                        <Col>
                        <Label htmlFor="rating"><h6>Rating</h6></Label>
                        <Control.select model=".rating" id="rating" className="form-control" name="rating">
                            <option> 1</option>
                            <option> 2</option>
                            <option> 3</option>
                            <option> 4</option>
                            <option> 5</option>
                        </Control.select>
                        </Col>
                        </Row>
                        <Row className="form-group">
                        <Col>
                        <Label htmlFor="author"><h6>Your Name</h6></Label>
                        <Control.text model=".author" id="author" className="form-control" name="author" validators={{required,minLength:minLength(3),maxLength:maxLength(15)}}/>
                        <Errors className="text-danger" model=".name" show="touched" messages={{
                            minLength:"Must be greater than 2 characters",
                            maxLength:"Must be less than or equal to 15 characters"
                        }}/>
                        </Col>
                        </Row>
                        <Row className="form-group">
                        <Col>
                        <Label htmlFor="comment"><h6>Commen</h6></Label>
                        <Control.textarea model=".comment" id="comment" className="form-control" name="comment" rows="6"/>                        
                        </Col>
                        </Row>
                        <Button type="submit" color="primary">Submit</Button>
                    </LocalForm>                    
                </ModalBody>
            </Modal>
            </React.Fragment>
        )
    }
}

export default DishDetail;